/* sidebar css start */
.sidebar {
  width: 250px;
  background: #ffffff;
  box-shadow: -8px 12px 18px 0 #b3bcd0;
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  z-index: 999;
  @include transition(all 0.3s);
  @media (max-width: 991px) {
    left: -285px; }
  &.open {
    left: 0; }
  &.bg_img {
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover; }
  .res-sidebar-close-btn {
    position: absolute;
    top: 0;
    right: -35px;
    width: 35px;
    height: 35px;
    background-color: $color--primary;
    color: #ffffff;
    display: none;
    @media (max-width: 991px) {
      display: block; }
    i {
      font-size: 24px; } }
  .sidebar__logo-shape {
    display: none;
    img {
      height: 35px; } }
  &.active {
    width: 80px;
    .sidebar__main-logo {
      display: none; }
    .sidebar__logo-shape {
      display: block; }
    .sidebar__menu-header {
      font-size: 0;
      text-align: center;
      margin: 5px 0;
      &::before {
          font-family: "Font Awesome 5 Free";
          content: "\f141";
          font-size: fsize("16px");
          color: $para-color; } }
    .menu-badge {
      display: none; }
    .sidebar__menu {
      .sidebar-menu-item {
        > a {
          padding: 15px;
          justify-content: center;
          .menu-icon {
            margin-right: 0; }
          .menu-title {
            display: none; } } }
      .sidebar-dropdown {
        > a {
          &::before {
            display: none; } } } }
    .sidebar-submenu {
      &.sidebar-submenu__open {
        display: none!important; }
      .sidebar-menu-item {
        a {
          padding: 10px 20px 10px 35px; } } }
    .navbar__expand {
      display: none; }
    &:hover {
      width: 250px;
      .navbar__expand {
        display: block; }
      .sidebar__main-logo {
        display: block; }
      .sidebar__logo-shape {
        display: none; }
      .sidebar__menu-header {
        font-size: 0.625rem;
        text-align: left;
        margin: 25px 0 5px 22px;
        &::before {
            display: none; } }
      .menu-badge {
        display: block; }
      .sidebar__menu {
        .sidebar-menu-item {
          > a {
            padding: 12px 20px;
            padding-right: 40px;
            justify-content: normal;
            .menu-icon {
              margin-right: 10px; }
            .menu-title {
              display: block; } } }
        .sidebar-dropdown {
          > a {
            &::before {
              display: block; } } } }
      .sidebar-submenu {
        &.sidebar-submenu__open {
          display: block!important; }
        .sidebar-menu-item {
          a {
            padding: 10px 20px 10px 35px; } } } } }
  .slimScrollDiv {
    .slimScrollBar {
      background-color: #000000!important;
      width: 3px!important;
      opacity: .25!important; } }
  &.capsule--rounded {
    .sidebar__menu {
      padding-left: 0;
      > .sidebar-menu-item {
        >a {
          margin-right: 5px;
          @include border-radius(0 999px 999px 0); }
        .sidebar-submenu {
          .sidebar-menu-item {
            margin-right: 0;
            > a {
              margin-right: 15px;
              @include border-radius(0 999px 999px 0); } } } } } }
  &.capsule--rounded2 {
    .sidebar__menu {
      > .sidebar-menu-item {
        >a {
          // margin: 0 15px
          @include border-radius(999px); }
        .sidebar-submenu {
          .sidebar-menu-item {
            margin-right: 0;
            > a {
              // margin: 0 15px
              @include border-radius(999px); } } } } } } }
.sidebar__logo {
  padding: 20px 10px;
  @include d-flex;
  align-items: center;
  text-align: center;
  .sidebar__main-logo {
    width: 90%;
    @media (max-width: 991px) {
      width: 100%; } }
  a {
    img {
 } } }      // height: 45px
.navbar__expand {
  margin-left: auto;
  background-color: transparent;
  width: 16px;
  height: 16px;
  // border: 1.5px solid $para-color
  @include border-radius(50%);
  position: relative;
  margin-top: 15px;
  @media (max-width: 991px) {
    display: none; }
  &.active {
    &::before {
      opacity: 0; } }
  &::before {
    position: absolute;
    content: '';
    top: 4px;
    left: 4px;
    width: 7px;
    height: 7px;
    background-color: $para-color;
    @include border-radius(50px);
    opacity: 1;
    @include transition(all 0.3s); }
  &::after {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 1.5px solid #ffffff;
    @include border-radius(50%); } }
.sidebar-submenu {
  display: none; }
.sidebar__menu {
  margin-top: 20px;
  padding: 0 10px;
  margin-bottom: 100px;
  .sidebar-menu-item {
    margin-top: 5px;
    > a {
      @include d-flex;
      align-items: center;
      padding: 12px 25px;
      transition: all 0.3s;
      @include border-radius(5px);
      &:hover {
        background-color: #EBECF0;
        padding-left: 25px;
        .menu-icon {
          color: $base-color;
          text-shadow: 1px 2px 5px rgba($base-color, 1); }
        .menu-title {
          color: $base-color; } } }
    .side-menu--open {
      background-color: #EBECF0; } }
  .menu-icon {
    color: #5b6e88;
    font-size: fsize("18px");
    margin-right: 15px;
    transition: all 0.5s;
    text-shadow: 1px 2px 5px rgba(#000000, 0.15); }
  .menu-title {
    font-size: fsize("13px");
    color: #5b6e88;
    font-weight: 300;
    letter-spacing: 0.5px; }
  .menu-badge {
    padding: 1px 6px;
    font-size: 0.625rem;
    font-weight: 500;
    border-radius: 3px;
    box-shadow: 0 4px 5px 0 rgba(0, 0, 0, 0.2); }
  .sidebar-dropdown {
    > a {
      padding-right: 40px;
      position: relative;
      &::before {
        position: absolute;
        top: 15px;
        right: 20px;
        font-family: "Font Awesome 5 Free";
        font-weight: 700;
        content: "\f107";
        font-size: 11px;
        color: #5b6e88; } } }
  .sidebar-submenu {
    position: relative;
    &::before {
      position: absolute;
      content: '';
      top: 0;
      left: 27px;
      width: 3px;
      height: 100%;
      border-left: 1px dashed #dcdcdc; }
    &__open {
      display: block; }
    .sidebar-menu-item {
      margin-top: 0;
      &.active {
        a {
          // background-color: $base-color
          .menu-icon {
            color: $base-color; }
          .menu-title {
            color: $base-color; } } }
      a {
        padding: 10px 20px 10px 35px;
        transition: all 0.3s;
        &:hover {
          // padding-left: 40px
          background-color: transparent!important;
          .menu-icon {
            color: $color--primary; }
          .menu-title {
            color: $color--primary; } }
        .menu-icon {
          font-size: 0.75rem; }
        .menu-title {
          font-size: fsize("12px"); } } } } }
.sidebar__menu-header {
  font-size: fsize("11px");
  text-transform: uppercase;
  font-weight: 700;
  color: #b5b5b5;
  margin: 25px 0 8px 25px; }
// bg variations
.sidebar {
  &[class*="bg--"] {
    .sidebar__logo {
      border-color: rgba(#ffffff, 0.15); }
    .sidebar__menu {
      .sidebar__menu-header {
        color: #b5b5b5; }
      .sidebar-menu-item {
        &.active > a, > a:hover {
          background-color: rgba(#ffffff, 0.12); }
        .menu-icon, .menu-title {
          color: #e0e0e0; }
        .side-menu--open {
          background-color: rgba(#ffffff, 0.12); } }
      .sidebar-dropdown > a::before {
        color: #e0e0e0; }
      .sidebar-submenu {
        &::before {
          border-left-color: #ffffff33; }
        .sidebar-menu-item {
          &.active, &:hover {
            a {
              .menu-icon, .menu-title {
                color: #ffffff; } } } } } } }
  &[class*="bg--"]:not([class*="bg--gradi"]).bg_img {
    background-image: none!important; }
  &[class*="bg--gradi"] {
    .sidebar__menu {
      .sidebar__menu-header {
        color: #dadada; } } }
  &[class*="bg--white"] {
    background-image: none!important;
    .sidebar__logo {
      border-color: #ececec; }
    .sidebar__menu {
      .sidebar__menu-header {
        color: #b5b5b5; }
      .sidebar-menu-item {
        &.active > a, > a:hover {
          background-color: #f5f5f5; }
        .menu-icon, .menu-title {
          color: #e0e0e0; }
        .side-menu--open {
          background-color: #f5f5f5; }
        &.active > a {
          background-color: #f5f5f5;
          &:hover {
            background-color: #f5f5f5; } }
        .sidebar-submenu__open {
          .sidebar-menu-item.active > a {
            background-color: transparent; } }
        &.open {
          background-color: #f5f5f5; }
        > a:hover {
          background-color: #EBECF0;
          .menu-icon, .menu-title {
            color: $base-color; } }
        .menu-icon, .menu-title {
          color: #5b6e88; } }
      .sidebar-dropdown > a::before {
        color: #5b6e88; } } }
  // for overlay
  &[class*="overlay"] {
    &::before {
      position: absolute;
      content: '';
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      opacity: 0.85;
      z-index: -10; }
    .navbar__expand {
      border-color: #ffffff;
      &::before {
        background-color: #ffffff; } }
    .sidebar__logo {
      border-color: rgba(#ffffff, 0.15); }
    .sidebar__menu {
      .sidebar__menu-header {
        color: #c3c3c3; }
      .sidebar-menu-item {
        &.active > a {
          background-color: rgba(#ffffff, 0.12);
          &:hover {
            background-color: rgba(#ffffff, 0.12); } }
        .sidebar-submenu__open {
          .sidebar-menu-item.active > a {
            background-color: transparent; } }
        > a {
          &.side-menu--open {
            background-color: rgba(#ffffff, 0.12); }
          &:hover {
            background-color: rgba(#ffffff, 0.12); } }
        .menu-icon, .menu-title {
          color: #e0e0e0; } }
      .sidebar-dropdown > a::before {
        color: #e0e0e0; }
      .sidebar-submenu {
        &::before {
          border-color: #dcdcdc5e; }
        .sidebar-menu-item {
          a {
            .menu-icon, .menu-title {
              color: #a9a9a9; } }
          &.active, &:hover {
            a {
              .menu-icon, .menu-title {
                color: #ffffff; } } } } } } }
  &[class*="overlay--white"] {
    .navbar__expand {
      border-color: $para-color;
      &::before {
        background-color: $para-color; } }
    .sidebar__logo {
      border-color: #dddddd; }
    .sidebar__menu {
      .sidebar__menu-header {
        color: #000000; }
      .sidebar-menu-item {
        &.active > a {
          background-color: rgba(#ffffff, 0.4);
          &:hover {
            background-color: rgba(#ffffff, 0.4); } }
        .sidebar-submenu__open {
          .sidebar-menu-item.active > a {
            background-color: transparent; } }
        a.side-menu--open {
          background-color: rgba(#ffffff, 0.4); }
        > a:hover {
          background-color: rgba(#ffffff, 0.4);
          .menu-icon, .menu-title {
            color: $base-color; } }
        .menu-icon, .menu-title {
          color: #363636; } }
      .sidebar-dropdown > a::before {
        color: #363636; }
      .sidebar-submenu {
        &::before {
          border-color: #dcdcdc; }
        .sidebar-menu-item {
          &.active , &:hover {
            a {
              .menu-icon, .menu-title {
                color: $base-color; } } } } } } }
  // menu capsule bg color variation
  &.pill--bg-1 {
    .sidebar-menu-item {
      .side-menu--open, &.active > a {
        background-color: $color--primary!important; }
      .sidebar-submenu__open {
        .sidebar-menu-item.active > a {
          background-color: transparent!important;
          background-image: none!important; } } } }
  &.pill--bg-2 {
    .sidebar-menu-item {
      .side-menu--open, &.active > a {
        background-color: $color--info!important; }
      .sidebar-submenu__open {
        .sidebar-menu-item.active > a {
          background-color: transparent!important;
          background-image: none!important; } } } }
  &.pill--bg-3 {
    .sidebar-menu-item {
      .side-menu--open, &.active > a {
        background-color: $color--danger!important; }
      .sidebar-submenu__open {
        .sidebar-menu-item.active > a {
          background-color: transparent!important;
          background-image: none!important; } } } }
  &.pill--gradi-1 {
    .sidebar-menu-item {
      .side-menu--open, &.active > a {
        @include gradient-one; }
      .sidebar-submenu__open {
        .sidebar-menu-item.active > a {
          background-color: transparent!important;
          background-image: none!important; } } } }
  &.pill--gradi-2 {
    .sidebar-menu-item {
      .side-menu--open, &.active > a {
        @include gradient-two; }
      .sidebar-submenu__open {
        .sidebar-menu-item.active > a {
          background-color: transparent!important;
          background-image: none!important; } } } }
  &.pill--gradi-3 {
    .sidebar-menu-item {
      .side-menu--open, &.active > a {
        @include gradient-three; }
      .sidebar-submenu__open {
        .sidebar-menu-item.active > a {
          background-color: transparent!important;
          background-image: none!important; } } } } }
/* sidebar css end */
